@import url(https://fonts.googleapis.com/css?family=Oregano:400i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  font-family: 'Oregano', cursive;
}


.buttons {
  padding: 10px;
  border: solid white 1px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: bounce infinite 1s ease-out;
          animation: bounce infinite 1s ease-out;
  height: 30vmin;
}

.outer-logo {
  -webkit-animation: outer-bounce infinite 2s linear;
          animation: outer-bounce infinite 2s linear;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 2vmin);
  color: white;
}

@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
  20% {
    -webkit-transform: translate3d(2px, 1px, 0);
            transform: translate3d(2px, 1px, 0);
  }
  40% {
    -webkit-transform: translate3d(1px, -2px, 0);
            transform: translate3d(1px, -2px, 0);
  }
  60% {
    -webkit-transform: translate3d(2px, 1px, 0);
            transform: translate3d(2px, 1px, 0);
  }
  80% {
    -webkit-transform: translate3d(-2px, 2px, 0);
            transform: translate3d(-2px, 2px, 0);
  }
  90% {
    -webkit-transform: translate3d(0px, -1px, 0);
            transform: translate3d(0px, -1px, 0);
  }
  100% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
}

@keyframes bounce {
  0% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
  20% {
    -webkit-transform: translate3d(2px, 1px, 0);
            transform: translate3d(2px, 1px, 0);
  }
  40% {
    -webkit-transform: translate3d(1px, -2px, 0);
            transform: translate3d(1px, -2px, 0);
  }
  60% {
    -webkit-transform: translate3d(2px, 1px, 0);
            transform: translate3d(2px, 1px, 0);
  }
  80% {
    -webkit-transform: translate3d(-2px, 2px, 0);
            transform: translate3d(-2px, 2px, 0);
  }
  90% {
    -webkit-transform: translate3d(0px, -1px, 0);
            transform: translate3d(0px, -1px, 0);
  }
  100% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
}

@-webkit-keyframes outer-bounce {
  0% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
  30% {
    -webkit-transform: translate3d(6px, -6px, 0);
            transform: translate3d(6px, -6px, 0);
  }
  100% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
}

@keyframes outer-bounce {
  0% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
  30% {
    -webkit-transform: translate3d(6px, -6px, 0);
            transform: translate3d(6px, -6px, 0);
  }
  100% {
    -webkit-transform: translate3d(0px, 0px, 0);
            transform: translate3d(0px, 0px, 0);
  }
}

